<template>
    <div class="content">
    <div class="header">
      <h2><img src="/images/headimg.png" alt=""/></h2>
      <div class="user-box">
        <a>昵称</a>
        <a href="javascript:;" @click="logout">注销</a>
      </div>
      <ul class="clear">
        <li>
          <span>0</span>
          <p>关注</p>
        </li>
        <li>
          <span>0</span>
          <p class="end">粉丝</p>
        </li>
      </ul>
    </div>
    <div class="docList">
      <ul>
        <li class="gk-text">
          <i></i>
          <p>公开博文</p>
          <b></b>
          <span>0</span>
        </li>
        <li class="mm-text">
          <i></i>
          <p>秘密博文</p>
          <b></b>
          <span>0</span>
        </li>
        <li class="cg-text">
          <i></i>
          <p>草稿箱</p>
          <b></b>
          <span>0</span>
        </li>
        <li class="sc-text">
          <i></i>
          <p>收藏夹</p>
          <b></b>
          <span>0</span>
        </li>
        <li class="my_cz">
          <i></i>
          <p>收藏夹</p>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>

import router from '../plugins/router'

export default {
  data(){
      return{
        user:{}
      }
  },
  beforeRouteEnter(to,from,next){   //前置守卫
      // 异步条件
      axios({
        url:'/api/user',

      }).then(
        res=>{
          if(res.data.error===0){  //是不是error？
            next(_this=>_this.user = res.data.data)
          }else{
            // this.$router.push('/login')   //错误姿势
            // $router.push('/login')  //也可以跳转，但需要引入router模块
            next('/login')       //正确姿势
          }
        }
      )
  },
  methods:{
    logout(){
        axios({
          url:'/api/logout',
          method:'put'
        }).then(
          res=>res.data.error === 0 && this.$router.push('/login')
        )
    }
  }
}
</script>


<style scoped>
@import url('../assets/css/user.css');
</style>
