<template>
    <div class="follow">
        <List :list="list" dataName="list"></List>
    </div>
</template>

<script>

import List from '../components/List'

export default {    
    data(){
        return{
            list:[]
        }
    }
    ,components:{
        List
    },

    mounted(){
        axios({
            url:'/api/list',
            params:{_page:1,_limit:6}  
        }).then((res) => {
            this.list=res.data.data
            
        })
    }
}
</script>
