<template>
    <div class="home">
        <Slider :banner="banner"></Slider>
        <List :list="list" dataName="list"></List>
    </div>
</template>

<script>

// import Slider from '../components/Silder'
import Slider from '@/components/Silder'
import List from '@/components/List'
// import 


export default {
    data(){
        return{
            list:[],
            banner:[]
        }
    },
    components:{
        Slider,List
    },
    mounted(){
        // console.log(this .$axios) //可以拿到，axios就在vue的实例里
        // console.log(axios)   // 拿不到，因为axios绑定在vue实例中
        axios({
            // url:'http://localhost:3000/api/home',
            url:'/api/list',  //因为有前端代理，所以直接访问/api就可以找到正确地址，在config.js中
            params:{_page:1,_limit:5}

        }).then(
            // res=>console.log(res.data)
            res=>this.list = res.data.data 

        )

   axios({
            url:'/api/list',  //因为有前端代理，所以直接访问/api就可以找到正确地址，在config.js中
            params:{_page:1,_limit:3}

        }).then(
            res=>this.banner = res.data.data 
        )

 

    }
}
</script>
