<template>
     <div class="banner" ref="f1">
    <ul class="clearfix">
      <router-link
         v-for="item of banner"
         :key="item._id" 
       tag="li" :to="`/detail/`+item._id">
        <img :src="baseUrl+item.ico" alt="">
        <div class="text-box">
          <h2>{{item.nei}}</h2>
          <p>{{item.wai}}</p>
        </div>
      </router-link>
      <!-- <router-link tag="li" to="/detail/2">
        <img src="/images/img_2.jpg" alt="">
        <div class="text-box">
          <h2>还可以降低首付款健身房222222</h2>
          <p>刘德华22 / 文</p>
        </div>
      </router-link> -->
     
    </ul>
  </div>
</template>

<script>
import $ from 'jquery'
import Swipe from '../../swipe'


export default {
props:['banner'],
    // mounted(){
    updated(){
        // console.log(1)
        // new Swipe($('.banner')[0], {
        // new Swipe(this.$refs.f1, {
        new Swipe($('.banner')[0] ,{
            
        autoplay: 2000,
        continuous: true,
        stopPropation: true,
        callback: function(index, element) {
            $(".banner ul li").removeClass("active");
            $(".banner ul li")
            .eq(index)
            .addClass("active");
        }
        });
    }
}
</script>


<style>
/* .content{max-width:6.4rem; margin:0 auto; margin-bottom:0.8rem;} */
.banner {
  margin-top: 0.6rem;
  position: relative;
  overflow: hidden;
  z-index: 1;
}
.banner ul li {
  width: 6.4rem;
  float: left;
  position: relative;
}
.banner ul li img {
  width: 100%;
  display: block;
}
.banner ul li .text-box {
  width: 5.8rem;
  padding: 0 0.3rem;
  height: 1.24rem;
  position: absolute;
  left: 0;
  bottom: 0;
  color: #fff;
  background: rgba(0, 0, 0, 0.5);
}
.text-box h2 {
  font-size: 0.3rem;
  font-weight: normal;
  margin-top: 0.22rem;
  line-height: 100%;
  margin-bottom: 0.22rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.text-box p {
  line-height: 100%;
}

.banner ol {
  position: absolute;
  right: 0.2rem;
  bottom: 0.2rem;
}
.banner ol li {
  width: 0.15rem;
  height: 0.15rem;
  background: #5477b2;
  float: left;
  border-radius: 50%;
  margin-right: 0.08rem;
}
.banner ol li.active {
  background: #fff;
}
</style>

